"use client"

import { useEffect, useState } from "react"

import { Button } from "@/components/ui/button"
import { useSidebar } from "@/components/ui/sidebar"

import { PanelLeftCloseIcon, PanelLeftIcon, SearchCheckIcon, SearchIcon } from "lucide-react"
import { DashboardCommand } from "./dashBoard-componets"



 

export const DashboardNavebar = () => {
  const { state, toggleSidebar, isMobile } = useSidebar();
  const [commandOpen, setCommandOpen] = useState(false);
  useEffect(() =>{
      const down =(e:KeyboardEvent)=> {
        if(e.key=="k" &&(e.metaKey || e.ctrlKey)){
          e.preventDefault();
          setCommandOpen((open)=>!open)
        }
      };document.addEventListener("keydown",down);
      return()=>document.removeEventListener("keydown",down)

    }, );
  return (
    <>
      <DashboardCommand open={commandOpen} setOpen={setCommandOpen} />
      <nav className="flex  px-5  gap-x-5 items-center py-3  border-b bg-background">
        <Button className="size-9 " variant="outline" onClick={toggleSidebar}>
          {(state === "collapsed" || isMobile) ?
            <PanelLeftIcon className="size-4" /> :
            <PanelLeftCloseIcon className="size-4" />}
        </Button>
        <Button
          className="h-9 w-[240px] justify-start font-normal text-muted-foreground hover:text-muted-foreground"
          variant="outline"
          size="sm"
          onClick={() => setCommandOpen((open)=>!open)}
        >
          <SearchIcon className="mr-2 h-4 w-4" />
          
          <kbd className="ml-auto pointer-events-none inline-flex h-5 select-none items-center gap-1 rounded border bg-muted px-1.5 font-mono text-[10px] font-medium text-muted-foreground">
            <span className="text-xs"></span>
            k
          </kbd>
        </Button>
      </nav>
    </>
  );
}